<template>
  <v-tab :items="tabItems">
    <div slot="generatecode">
      <v-input-txt
        label="/Url"
        placeholder=""
        v-model="dataTxt"
      ></v-input-txt>
      <div class="layui-form-item layui-form-text">

        <input
          type="color"
          class="layui-input"
          placeholder=" #ffffff"
          v-model="color"
          @change="convert(dataTxt,color,bgcolor)"
        />
      </div>
      <div class="layui-form-item layui-form-text">
        <input
          type="color"
          class="layui-input"
          placeholder=" #1aa094"
          v-model="bgcolor"
          @change="convert(dataTxt,color,bgcolor)"
        />
      </div>
      <!-- <div class="layui-form-item">
        <v-button @click="convert(dataTxt,color,bgcolor)"></v-button>
      </div> -->
      <div class="layui-form-item">
        <img :src="qrcodeImg" />
      </div>
    </div>
    <div slot="decode">
      <div
        id="qrfile"
        qrCanvas="qr-canvas"
        qrResult="qrcode_content"
      >
        <div title="">
          <div
            class="browser"
            id="canvas_ext_div"
          >
            <label style="height:30px;line-height:30px;">
              <span></span>
              <input
                type="file"
                name="files"
                accept="image/*"
                id="ewmfile"
                title=""
              >
            </label>
          </div>
        </div>
        <canvas
          style="border: 1px solid #CCC;"
          id="qr-canvas"
        ></canvas>
      </div>
      <div>
        <p
          id="qrcode_content"
          style="border: 1px solid #ccc;line-height: 30px;"
        ></p>
      </div>
    </div>
  </v-tab>
</template>
<script>
import jrQrcode from "jr-qrcode";
import webqr from "../assets/qrcode/webqr.js";
import common from "../utils/common";
export default {
  meta: {
    menuName: "/",
    sort: 101
  },
  data() {
    return {
      tabItems: [
        {
          Name: "generatecode",
          Title: ""
        },
        {
          Name: "decode",
          Title: ""
        }
      ],
      qrcodeImg: "",
      dataTxt: "",
      color: "#ffffff",
      bgcolor: "#1aa094",
      imgText: ""
    };
  },
  created() {
    let self = this;
    window.webqr = webqr;
    if (typeof chrome != undefined && chrome.tabs) {
      chrome.tabs.getSelected(function(tab) {
        self.dataTxt = tab.url;
      });
    } else {
      self.dataTxt = location.href;
    }
    this.$nextTick(() => {
      layui.jquery("#ewmfile").change(function() {
        console.log(this.files);
        webqr.handleFiles(this.files);
      });
      this.selectDecode();
      this.pasteInit();
    });
  },
  watch: {
    dataTxt: "zidong"
  },
  methods: {
    zidong() {
      this.convert(this.dataTxt, this.color, this.bgcolor);
    },
    convert(txt, color, bgcolor) {
      var imgBase64 = jrQrcode.getQrBase64(txt, {
        padding: 10, //10px
        width: 256, //256px
        height: 256, //256px
        correctLevel: QRErrorCorrectLevel.H, //level
        background: color || "#ffffff", //
        foreground: bgcolor || "#1aa094" //
      });
      this.qrcodeImg = imgBase64;
    },
    selectDecode() {
      webqr.load("qrfile", 200, 200);
    },
    pasteInit() {
      document.addEventListener("paste", function(e) {
        if (!(e.clipboardData && e.clipboardData.items)) {
          return;
        }
        for (var i = 0, len = e.clipboardData.items.length; i < len; i++) {
          var item = e.clipboardData.items[i];
          if (item.kind === "string") {
            item.getAsString(function(str) {
              // str 
            });
          } else if (item.kind === "file") {
            var pasteFile = item.getAsFile();
            // pasteFile
            webqr.handleFiles([pasteFile]);
          }
        }
      });
    }
  }
};
</script>


